
import React, { Component } from "react";

import "bootstrap/dist/css/bootstrap.min.css";
import "./Components.css";

class Subjects extends Component {
	
	render() {
		const { nodesData, selected, sortBy, funcSelect, funcSort, funcClear } = this.props;	
		
		let data = nodesData.sort((a, b) => {
			switch (sortBy) {
				case "code":
					return a.code.localeCompare(b.code)
				case "title":
					return a.title.localeCompare(b.title)
				case "semester":
					return a.semester.localeCompare(b.semester);
				case "priority":
					return a.priority.localeCompare(b.priority);
			}
		});
			
		const c1 = {width: "15%"};
		const c2 = {};
		const c3 = {width: "20%"};
		const c4 = {width: "20%"};
		
		const trStyle = (pred) => {
		  return {
			// backgroundColor: "red",
        	filter: "brightness(" + (pred ? "80" : "100") + "%)",
      	};
		};
		
		const link = (code) => "https://www.uio.no/studier/emner/matnat/ifi/" +code +"/index.html"
		
		// console.log(mand)
		
	    const idk = data.map((row) => {
			
			
			const isSelected = selected.map((d) => d.id).includes(row.id) // || mand.includes(row.id);
			
	      return (
			<>
	          <tr key={row.id} data-item={row.code} onClick={funcSelect} style={trStyle(isSelected)}>
	            <td style={c1}><a style={{color:"black"}} href={link(row.code)} target="_blank" rel="noopener noreferrer">{row.code}</a></td>
				<td style={c2}>{row.title}</td>
				<td style={c3}>{row.semester}</td>
				<td style={c4}>{row.requires != null ? row.requires.join(", ") : ""}</td>
	          </tr>
			</>
        );
	    });
		
		return (
			<div className="subject-container">
		      <table className="subject-table">
		        <thead>
		          <tr>
		            <th style={c1} data-item={"code"} onClick={funcSort}>code</th>
		            <th style={c2} data-item={"title"} onClick={funcSort}>title</th>
					<th style={c3} data-item={"semester"} onClick={funcSort}>semester</th>
					<th style={c4} data-item={"priority"} onClick={funcSort}>priority</th>
		          </tr>
		        </thead>
		        <tbody>
		          {idk}
		        </tbody>
		      </table>
			  <div className="spacer"/>
			  <button className="subject-button" onClick={funcClear}>clear</button>
			</div>
    );
	}
}

export default Subjects