
import React, { Component } from 'react'
import MyNavbar from './components/Navbar'

import { ForceGraph } from './components/graphing/ForceGraph'
import data from './data/sample.json'
import Subjects from './components/Subjects'
import SemesterPlan from './components/Semesterplan'

class App extends Component {
  state = {
    subs: [],
	topsorted: [],
	// selected: [],
	sortBy: "",
    // thisfunc: undefined,
  };

  //
  addSubs = (e) => {
    const node = e.currentTarget.getAttribute("data-item");
    const temp = data.nodes.filter((a) => a.code === node)[0];

    const { subs, thisfunc } = this.state;

	// const allIDS = [].concat(...subs.map((n) => n.msubs)).filter((n) => n != null);
	// const idk = temp.msubs 
	// const allNodes = data.nodes.filter((n) => temp.msubs != null ? temp.msubs.includes(n.id) : false);
	
	// console.log(allNodes)
	
    if (!subs.includes(temp)) {
		const result = this.recursiveAdd([temp])
		const result2 = result.filter((n) => !subs.includes(n))
		const result3 = [...new Set(result2)]
		
		this.setState({
        	subs: [...this.state.subs, ...result3],
      	});
    }
	
	else {
		this.setState({
			subs: subs.filter((sub) => sub !== temp),
		});
	}
	thisfunc()
  };
  
  recursiveAdd = (nodeList) => {
	
	let result = [...nodeList]
	
	for (const node of nodeList) {
		
		if (node.msubs != null) {
			let rec = data.nodes.filter((n) => node.msubs.includes(n.id))
			result = [...result, ...this.recursiveAdd(rec)];
		}
		
		if (node.rsubs != null) {
			let rec = data.nodes.filter((n) => node.rsubs.includes(n.id))
			result = [...result, ...this.recursiveAdd(rec)];
		}
	}
	
	return result
  }
  
  clearSubs = () => {
		const { thisfunc } = this.state;
	
		this.setState({
			subs: [],
		});
		
		thisfunc()
  }

  //
//   removeSubs = (e) => {
//     const node = e.currentTarget.getAttribute("data-item");
//     const temp = data.nodes.filter((a) => a.code === node)[0];

//     const { subs, thisfunc } = this.state;
	
// 	console.log("remove?", temp, subs)

//     this.setState({
//       subs: subs.filter((sub, _) => {
//         return sub.id !== temp.id;
//       }),
//     });
// 	thisfunc();
//   };

  //
  setFunc = (e) => {
    this.setState({
      thisfunc: e,
    });
  };
  
    //
  tsFunc = (e) => {
    this.setState({
      topsorted: e,
    });
  };
  
  chooseSort = (e) => {
	// console.log(e)
	this.setState({
		sortBy: e.currentTarget.getAttribute("data-item")
	})
  }

  //
  nodeHoverTooltip = (node) => {
	return node.id + " " + node.title
  }

  render() {
    const { subs, sortBy, topsorted, selected, thisfunc } = this.state;

    return (
      <div className="App">
        <MyNavbar></MyNavbar>
        <div className="app-container">
          <section className="left">
            <Subjects
              nodesData={data.nodes}
              selected={subs}
              sortBy={sortBy}
              funcSelect={this.addSubs}
              funcSort={this.chooseSort}
			  funcClear={this.clearSubs}
            />
            {/* <div className="spacer"/> */}
            {/* <Subjects myfunc={this.removeSubs} nodesData={subs} /> */}
            {/* <button onClick={thisfunc}>CLICK!!!</button> */}
          </section>

          <section className="middle">
            {/* <SemesterPlan
				nodesData={subs}
				linksData={data.links}
				result={topsorted}
				tsFunc={this.tsFunc}
			/> */}
          </section>

          <section className="right">
            <ForceGraph
              linksData={data.links}
              nodesData={subs}
              nodeHoverTooltip={this.nodeHoverTooltip}
              mytemp={this.setFunc}
            />
          </section>
        </div>
      </div>
    );
  }
}

export default App